import React, { useState, useEffect } from "react";
import "./herostyles.css";

const Hero = ({ heroImage }) => {
  const [isMobile, setIsMobile] = useState(false);
  const heroText1Desktop = "Kissakahvila";
  const heroText1Mobile = "Kissa-";
  const heroText2 = "kahvila";
  const heroText3 = "- JOENSUUSSA -";

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 504);
    };

    handleResize();
    window.addEventListener("resize", handleResize);
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  return (
    <div className="hero">
      <img src={heroImage} className="hero-img" alt="Header" />
      <div className="hero-text-container">
        {isMobile ? (
          <div className="hero-text1">
            {heroText1Mobile}
            {heroText2}
          </div>
        ) : (
          <div className="hero-text1">{heroText1Desktop}</div>
        )}
        <div className="hero-text2">{heroText3}</div>
      </div>
    </div>
  );
};

export default Hero;
