import React, { useState } from "react";
import Fbicon from "../images/fbicon.svg";
import Igicon from "../images/igicon.svg";
import Fbiconhover from "../images/fbiconhover.svg";
import Igiconhover from "../images/igiconhover.svg";
import "./footerstyles.css";

const Someicons = () => {
  const [fbHovered, setFbHovered] = useState(false);
  const [igHovered, setIgHovered] = useState(false);

  const fbIconToShow = fbHovered ? Fbiconhover : Fbicon;
  const igIconToShow = igHovered ? Igiconhover : Igicon;

  return (
    <div>
      <a
        href="https://www.facebook.com"
        onMouseEnter={() => setFbHovered(true)}
        onMouseLeave={() => setFbHovered(false)}
      >
        <img className="some-icon" src={fbIconToShow} alt="Facebook" />
      </a>

      <a
        href="https://www.instagram.com"
        onMouseEnter={() => setIgHovered(true)}
        onMouseLeave={() => setIgHovered(false)}
        target="_blank"
        rel="noreferrer"
      >
        <img className="some-icon" src={igIconToShow} alt="Instagram" />
      </a>
    </div>
  );
};

export default Someicons;
