import React, { useState } from "react";
import "./formstyles.css"; // Import CSS file for styling

function Form() {
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    subject: "",
    message: "",
  });
  
  const [messageSent, setMessageSent] = useState(false); // State to control message visibility

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const handleCancel = (e) => {
      // Clear the form fields
      setFormData({
        name: "",
        email: "",
        subject: "",
        message: "",
      });
    };

  const handleSubmit = (e) => {
    e.preventDefault(); // Prevent default form submission behavior

    // Here you can perform any actions you want with the form data, like sending it to a server

    // Clear the form fields
    setFormData({
      name: "",
      email: "",
      subject: "",
      message: "",
    });

    // Show the message
    setMessageSent(true);

    // Hide the message after 3 seconds
    setTimeout(() => {
      setMessageSent(false);
    }, 3000);
  };

  return (
    <div className="form">
    <form onSubmit={handleSubmit}>
      <div>
        <label htmlFor="name">Nimi</label><br></br>
        <input
          type="text"
          id="name"
          name="name"
          value={formData.name}
          onChange={handleChange}
          required
          className="textarea"
        />
      </div>
      <div>
        <label htmlFor="email">Shkpostiosoite</label><br></br>
        <input
          type="email"
          id="email"
          name="email"
          value={formData.email}
          onChange={handleChange}
          required
          className="textarea"
        />
      </div>
      <div>
        <label htmlFor="subject">Aihe</label><br></br>
        <input
          type="text"
          id="subject"
          name="subject"
          value={formData.subject}
          onChange={handleChange}
          required
          className="textarea"
        />
      </div>
      <div>
        <label htmlFor="message">Viesti</label><br></br>
        <textarea
          id="message"
          name="message"
          value={formData.message}
          onChange={handleChange}
          required
          className="large-textarea"
        />
      </div>
      <div>
        <button type="submit" className="submit-button">Lhet</button>
        <button type="cancel" className="submit-button" onClick={handleCancel}>Tyhjenn</button>
        {messageSent && <p>Viesti lhetettiin! (leikisti)</p>}
      </div>
    </form>
    </div>
  );
}

export default Form;
